import { Inline, Controller, ConvertToPluginActionCreators } from '@ali/4ever-cangjie';

export const IMAGE_SELECT = Symbol('IMAGE_SELECT');

export const IMAGE_REMOVE = Symbol('IMAGE_REMOVE');

export const IMAGE_UPDATE = Symbol('IMAGE_UPDATE');

export const IMAGE_SET_INJECTION = Symbol('IMAGE_SET_INJECTION');

/**
 * 插入上传图片的 placeholder
 */
export const INSERT_IMAGE_PLACEHOLDER = Symbol('INSERT_IMAGE_PLACEHOLDER');

/**
 * 唤起图片格式侧边栏
 */
export const TOGGLE_IMAGE_SIDEBAR = Symbol('TOGGLE_IMAGE_SIDEBAR');

export function selectImage(placeholderKey?: string) {
  return {
    type: IMAGE_SELECT,
    payload: {
      placeholderKey,
    },
  };
}

export function removeImage(image) {
  return {
    type: IMAGE_REMOVE,
    payload: {
      node: image,
    },
  };
}

export function updateImage(image, data, container?: HTMLElement | null) {
  return {
    type: IMAGE_UPDATE,
    payload: {
      node: image,
      data,
      container,
    },
  };
}

export function setImageInjection(key, data) {
  return {
    type: IMAGE_SET_INJECTION,
    payload: {
      key,
      data,
    },
  };
}

export interface ToggleImageSidebarPayload {
  node?: Inline | null;
  toggle: boolean;
  callback?: (controller: Controller) => void;
}

export function toggleImageSidebar(payload: ToggleImageSidebarPayload) {
  return {
    type: TOGGLE_IMAGE_SIDEBAR,
    payload,
  };
}


export const actions = {
  selectImage,
  removeImage,
  updateImage,
  setImageInjection,
  toggleImageSidebar,
};

/** @deprecated */
export const DEPRECATED_ACTIONS = {
  IMAGE_SELECT,
  IMAGE_REMOVE,
  IMAGE_UPDATE,
  IMAGE_SET_INJECTION,
  INSERT_IMAGE_PLACEHOLDER,
  TOGGLE_IMAGE_SIDEBAR,

  selectImage,
  removeImage,
  updateImage,
  setImageInjection,
  toggleImageSidebar,
};

declare module '@ali/4ever-cangjie' {
  interface PluginActionCreators extends ConvertToPluginActionCreators<typeof actions> {}
}
